import { useSelector } from "react-redux";
import { Link } from "react-router-dom";

const AppSidebar = () => {
    const isLoggedIn = useSelector(state => state.auth.isLoggedIn);
    return (
        isLoggedIn && (
            <aside id="sidebar" className="sidebar">
                <ul className="sidebar-nav" id="sidebar-nav">
                    <li className="nav-item">
                        <Link className="nav-link collapsed" to="/">
                            <i className="bi bi-grid"></i>
                            <span>Dashboard</span>
                        </Link>
                    </li>
                    <li className="nav-item">
                        <span className="nav-link collapsed" data-bs-target="#forms-nav" data-bs-toggle="collapse" href="#">
                            <i className="bi bi-journal-text"></i><span>Links</span><i className="bi bi-chevron-down ms-auto"></i>
                        </span>
                        <ul id="forms-nav" className="nav-content collapse " data-bs-parent="#sidebar-nav">
                            <li>
                                <Link to="/tabs">
                                    <i className="bi bi-circle"></i><span>Tabs</span>
                                </Link>
                            </li>
                            <li>
                                <Link to="/table">
                                    <i className="bi bi-circle"></i><span>Table</span>
                                </Link>
                            </li>
                        </ul>
                    </li>
                </ul>
            </aside>
        )
    )
};

export default AppSidebar;